.banner{
    margin-top: -10vh;
    height: 80vh;;
    /* margin-bottom: 0px; */
    color: rgb(248, 242, 242);
    object-fit:contain;
}
.banner_contents{
    margin-left: 30px;
    margin: bottom 2px;;
    padding-top: 300px;
    /* height: 190px; */
}
.banner_title{
    font-size: 2rem;
    font-weight: 800;
    padding-bottom: 0.4rem;
}
.banner_description{
    width: 4rem;
    line-height: 1;
    padding: 1rem 0;
    font-size:1rem;
    min-width: 360px;
    height: 80px;
}
.banner_button{
    cursor: pointer;
    color: #111;
    outline: none;
    border: none;
    font-size: 1.2rem;
    font-weight: 700;
    border-radius: 0.2vw;
    padding: 1rem 3rem;
    margin-right: 2rem;
    background-color: rgba(51, 51, 51, 0.5);
}
.play{
    color: #000;
    background-color: #fff;

}
.banner_button:hover {
    color: #000;
    background-color: #c6c6c6;
    transition: a11 0.2s;
}
.banner_fadeBottom{
    height: 40vh;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgb(37, 37, 37, 0.91),
        #111
    );
}
