  .header_outer_container {
    position: sticky;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #c10b0b;
background-color: transparent;
height: 10vh;
color: #0dd0e6;
transition: all 0.5s;
transition-timing-function: ease-in;
z-index: index 1;;
/* background-color: red; */
}
.nav_black {
    background-color: rgb(20, 20, 20);
}
.header_container{
width: 90%;
display: flex;
justify-content: space-between;
align-items: center;
}

.header_left ul {
    width: 140%;
    display: flex;
    align-items: center;
    list-style: none;
    list-style: none;
    justify-content: space-between;
    cursor: pointer;
}

.header-right ul {
    width: 140%;
    display: flex;
    align-items: center;
    list-style: none;
    list-style: none;
    justify-content: space-between;
}
a:visited {
    color: rgb(235, 247, 234);
    background-color: transparent;
    text-decoration: none;
   
}
a:hover{ color: #87d0fa; 
}


@media only screen and (max-width:720px) {
    .header_outer_container {
        position: sticky;
        top: 0;
        display:block;
        justify-content: center;
        align-items: center;
        background-color: rgb(12, 1, 1);
    background-color: transparent;
    height: 10vh;
    color: #f6f7f8;
    transition: all 0.5s;
    transition-timing-function: ease-in;
    z-index:1;
    /* background-color: red; */
    }
    .nav_black {
        background-color: rgb(20, 20, 20);
    }
    .header_container{
    width: 100%;
    display:block;
    justify-content: space-between;
    align-items: center;
    }
    
    .header_left ul {
        width:125%;
        display:flex;
        align-items: center;
        list-style: none;
        list-style: none;
        justify-content: space-between;
        cursor: pointer;
    }
    
    .header-right ul {
        width: 100%;
        display: flex;
        align-items:center;
        list-style: none;
        list-style: none;
        justify-content: space-between;
    }
    a:visited {
        color: rgb(235, 247, 234);
        background-color: transparent;
        text-decoration: none;
       
    }
    a:hover{ color: #87d0fa; 
    }
    img{
        width: 80px;;
        position:to;
        
    
    }
}



