
@media only screen and (max-width:720px) {
  body {
    background-color: rgb(246, 15, 173);
  
    .row_poster {
      width: 100%;
      object-fit: contain;
      max-height: 100px;
      transition: transform 450ms;
      margin-right: 10px;
    }
    .row_posters{
      display:inline-block;
      overflow-y: 0; 
      overflow-x: hidden;
      padding: 5px;}
    .row_posters::-webkit-scrollbar{
        display: none;
    }
    .row_poster:hover{
        transform: scale(1.08);
    }
    .row_posterLarge {
        max-height: 200px;
    }
    .row_posterLarge:hover
    {transform: scale(1.09);}
      .row{
        margin:center;
        color: rgb(10, 240, 10);
        font-size:small;
        text-align: center;
    
        
      }
  }
}
